.inputContainer {
  border: 1px solid rgb(114, 114, 114);
  padding: 20px;
  background-color: #fcd9d0;
  border-radius: 5px;
  label {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    font-weight: 600;
  }
  input {
    width: 175px;
    background-color: #fcfaf3;
    border: 2px solid #2b1506;
    border-radius: 3px;
    border-width: 0;
    border: 1px solid #dad7cf;
    padding: 3px 2px;
    &:focus {
      outline: none;
    }
  }
  button {
    position: absolute;
    left: 155px;
    padding-top: 4px;
    background: none;
    border: none;
    color: rgb(192, 192, 192);
    &:focus {
      outline: 0;
      outline-offset: 0;
    }
    svg {
      width: 15px;
      height: 15px;
    }
  }
}
.formBox {
  position: relative;
  display: flex;
  justify-content: left;
  align-items: center;
  margin-top: 10px;
}
